.App {
  text-align: center;
  height: 100%;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html, body {
  overflow: hidden;
  height: 100%;
}

#root {
  height: 100%;
}

.TaskList {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  display: flex;
  justify-content: space-around;
}

.UserForm {
  display: flex;
  flex-direction: column;
}

.Tasks {
  overflow: auto;
  flex: 1;
}

.Task {
  display: flex;
  justify-content: space-around;
}

.parent {
  background-color: lightgray;
}